<template>
   <el-card>
       <!-- 面包屑区域 -->
       <slot-common slot="header" style="font-size:18px">
           <template slot="title">内容列表</template>
       </slot-common>
       <!-- form表单区域 -->
       <el-form>
         <el-form-item label="文章状态">
               <el-radio-group v-model="formData.status" @change="refreshList">
                 <!-- label必须有值 -->
               <el-radio :label="5">全部</el-radio>
               <el-radio :label="0">草稿</el-radio>
               <el-radio :label="1">待审核</el-radio>
               <el-radio :label="2">审核成功</el-radio>
               <el-radio :label="3">审核失败</el-radio>
           </el-radio-group>
         </el-form-item>
         <el-form-item label="频道列表">
             <el-select  @change="refreshList" v-model="formData.channel_id" placeholder="请选择">
                 <el-option :label="item.name" :value="item.id" v-for="item in channels" :key="item.id"></el-option>
             </el-select>
         </el-form-item>
         <el-form-item label="时间选择">
            <el-date-picker
                @change="refreshList"
                v-model="formData.dataRange"
                value-format="yyyy-MM-dd"
                type="daterange"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
          </el-date-picker>
         </el-form-item>
       </el-form>
       <div class="content-title">共找到{{page.total}}条符合条件的内容</div>
       <div class="content">
        <!-- 循环项 -->
          <div class="content-item" v-for="(item,index) in contentList" :key="index">
              <!-- 左侧部分 -->
              <div class="content-left">
                  <img :src="item.cover.images[0]" alt="">
                  <!-- 左侧内容部分 -->
                  <div class="content-info">
                    <span>{{item.title}}</span>
                    <el-tag style="width:60px;" :type='item.status|statusType'>{{item.status|statusText}}</el-tag>
                    <span class="data">{{item.pubdate}}</span>
                  </div>
              </div>
              <!-- 右侧部分 -->
              <div class="content-right">
                <span style='cursor:pointer'>
                    <i class="el-icon-edit"></i>
                   修改
                 </span>
                 <span style='cursor:pointer' @click="delItem(item.id)">
                    <i class="el-icon-delete"></i>
                    删除
                  </span>
              </div>
          </div>
       </div>
       <!-- 分页组件 -->
       <el-row type="flex" justify="center" style="margin:30px 0;">
         <el-pagination
         @current-change="changePage"
         :page-size="page.pageSize"
         :current-page="page.currentPage"
          background
          layout="prev, pager, next"
          :total="page.total">
        </el-pagination>
       </el-row>
   </el-card>
</template>

<script>
export default {
  data () {
    return {
      contentList: [],
      channels: [],
      formData: {
        status: 5,
        channel_id: null,
        dataRange: null
      },
      page: {
        total: 0, // 总条数
        pageSize: 10, // 默认显示每页10条内容
        currentPage: 1 // 当前页数
      }
    }
  },
  methods: {
    // 页码改变事件
    changePage (newPage) {
      // console.log(newPage)
      this.page.currentPage = newPage // 获取当前页码
      // 点击某一页会把某一页的全部条件加到一起发送个给方法
      this.getArticles(this.condition())
    },
    condition () {
      let { status, channel_id: cid, dataRange } = this.formData // 解构赋值
      let params = {
        status: status === 5 ? null : status, // 由于默认值给了5 ， 如果是5 就不能传，
        channel_id: cid,
        begin_pubdate: dataRange && dataRange.length ? dataRange[0] : null,
        end_pubdate: dataRange && dataRange.length > 1 ? dataRange[1] : null
      }
      params.page = this.page.currentPage
      params.per_page = this.page.pageSize
      return params
    },
    // 刷新列表数据 状态改变 、 频道切换 、 日期改变都会触发
    refreshList () {
      // 但筛选时应该讲页码归到第一页
      this.page.currentPage = 1
      this.getArticles(this.condition())
    },
    // 删除列表
    delItem (id) {
      this.$confirm('您确定要删除此条内容么？', '提示').then(() => {
        this.$http({
          method: 'delete',
          url: `/articles/${id.toString()}`
        }).then(() => {
          // 如果删除成功调用获取内容函数
          this.getArticles()
        })
      })
    },
    // 获取频道
    getChannels () {
      this.$http({
        url: '/channels'
      }).then(result => {
        // console.log(result.data.channels)
        // 将获取到的频道赋值给channels
        this.channels = result.data.channels
      })
    },
    // 获取内容
    getArticles (params) {
      this.$http({
        url: '/articles',
        params: { ...params }
      }).then(result => {
        // console.log(result.data.results)
        // 将获取到的文章内容赋值给contentList
        this.contentList = result.data.results
        this.page.total = result.data.total_count // 文章总数
      })
    }
  },
  // 过滤器
  filters: {
    // 文章状态
    statusText (value) {
      switch (value) {
        case 0:
          return '草稿'
        case 1:
          return '待审核'
        case 2:
          return '已发布'
        case 3:
          return '审核失败'
        case 4:
          return '已删除'
      }
    },
    // type控制tag类型，定义背景颜色
    statusType (color) {
      switch (color) {
        case 0:
          return 'warning'
        case 1:
          return 'info'
        case 2:
          return 'success'
        case 3:
          return 'danger'
        case 4:
          return 'danger'
      }
    }
  },
  created () {
    this.getChannels()
    this.getArticles()
  }
}
</script>

<style lang="less" scoped>
.content-title{
  border-bottom: 1px dashed #ccc;
  height: 60px;
  line-height: 60px;
}
.content {
  .content-item {
    padding: 10px;
    border-bottom: 1px solid #f2f3f5;
    display: flex;
    justify-content: space-between;
    .content-left {
       display: flex;
      align-items: center;
      img {
        width: 150px;
        height: 100px;
        border-radius: 4px;
      }
      .content-info {
        margin-left: 10px;
        display: flex;
        height: 100px;
        padding: 5px 0;
        flex-direction: column;
        justify-content: space-between;
        .data {
          font-size: 12px;
          color: #999;
        }
      }
    }
    .content-right {
      line-height: 100px;
      margin-right: 10px;
        span,
      span i {
        font-size: 12px;
        color: #333;
      }
      span {
        margin-right: 5px;
        margin: 0 10px;
      }
    }
  }
}
</style>
